@use 'base/variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.2rem 3rem;
    color: $colorTexto;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    border-radius: 4px;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 95%;
    margin: 0 auto;

    @include tablet {
        width: auto;
        margin: 0;
    }

    &:hover {
        background-color: darken($bgColor, 10%);
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin degradado($color, $nextColor) {
    font-family: v.$fuente_headings;
    font-size: 5rem;
    margin: 0;
    background: linear-gradient(to right, $color 0%, lighten($nextColor, 30%) 100%);
    color: transparent;
    background-clip: text;
}