@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {

    text-align: center;
    margin-top: 3rem;
    width: 100%;


    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: .5rem;
        height: 100vh;
        text-align: start;
    }


    .sidebar {
        border: 1px solid #2c2c2c;
        padding: 2rem;
        background: linear-gradient(to bottom, v.$grisClaro 50%, v.$grisOscuro 100%);
        height: 100%;
        border-radius: 10px;

        @include m.tablet {

            box-shadow: 25px -15px 10px -20px v.$grisOscuro;
        }

        h2 {
            color: v.$blanco;
        }

        .sidebar-nav {
            display: flex;
            flex-direction: column;
            gap: 2rem;

            a {
                color: v.$blanco;
                text-decoration: none;
                padding: 1rem;
                transition: all .3s ease;

                &.activo {
                    background-color: v.$gris;
                    border-radius: 20px;

                    span {
                        color: v.$blanco;
                        text-align: center;
                    }

                }

                span {
                    display: block;

                    @include m.tablet {
                        text-align: left;
                    }
                }

                &:hover {
                    background-color: darken(v.$blanco, 10);
                    border-radius: 20px;

                    span {
                        color: v.$negro;
                    }
                }

                @include m.tablet {

                    &:hover span {
                        text-align: center;
                    }

                }

            }
        }

    }


    .principal {
        margin: 2rem 0;
        padding: 0 1rem;


        @include m.tablet {
            flex-basis: 100%;
            margin: 0;
        }

        .contenido {
            margin-top: 1rem;
            padding: 2rem 0;
            text-align: center;




            .formulario {
                margin-bottom: 2rem;

                @include m.tablet {
                    position: relative;
                    min-height: 300px;


                    input[type="submit"] {
                        background-color: red;
                        position: absolute;
                        right: 0;
                    }
                }


                input:not([type="submit"]) {
                    background-color: v.$blanco;
                    outline: 1px solid v.$gris;
                }


            }
        }


    }

    .listar-proyectos {
        list-style: none;
        width: 100%;


        .proyectos {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            background: linear-gradient(to right, v.$gris 20%, v.$grisClaro 100%);
            padding: 1.5rem;
            border-radius: 10px;

            @include m.tablet {
                flex-direction: row;
                box-shadow: 5px 10px 10px 1px v.$grisOscuro;
            }

            .nombre-proyecto {
                font-size: 2.5rem;
                font-weight: bold;
                color: v.$cyan
            }

            div {
                display: flex;
                gap: 1rem;
                flex-basis: 100%;

                p {
                    font-weight: bold;
                    text-transform: uppercase;
                    color: #fff;
                }
            }
        }

        .proyectos:not(:last-child) {
            margin-bottom: 4rem;
        }

    }




    .contenedor-nueva-tarea {
        display: flex;
        justify-content: center;
        gap: 2rem;

        .agregar-tarea {
            @include m.boton(v.$cyan, v.$blanco);
        }

        .eliminar-tarea {
            @include m.boton(v.$rojo, v.$blanco);
        }

    }

    .listar-tareas {
        margin: 3rem auto;
        width: 100%;


        thead {
            tr {
                display: flex;
                justify-content: space-between;
                padding: 1rem 0;
                border: 1px solid v.$cyan;

                th {

                    font-weight: 700;
                    width: min(150px, 100%);

                    @include m.tablet {
                        font-size: 2.5rem;
                    }
                }
            }
        }

        tbody {

            tr {
                display: flex;
                justify-content: space-between;
                padding: 1rem 0;

                &:not(:last-child) {
                    border-bottom: 1px solid v.$gris;
                }

                td {
                    width: min(150px, 100%);

                    @include m.tablet {
                        font-size: 2rem;
                    }
                }

                .acciones-tarea {
                    display: flex;
                    justify-content: space-around;

                    a {
                        cursor: pointer;
                    }
                }

            }

        }



    }

}

.sinproyectos {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;

    @include m.tablet {
        flex-direction: row;
    }


}