  margin: 0;
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
}
.titulo .descripcion-pagina {
  font-family: "Inter", sans-serif;
  font-size: 2rem;
  font-weight: 900;
  color: #000;
}

.titulo.crear .nombre-pagina {
  font-family: "Inter", sans-serif;
  font-size: 5rem;
  margin: 0;
  background: linear-gradient(to right, #7C3AED 0%, #d9c6fa 100%);
  color: transparent;
  background-clip: text;
}

.titulo.recuperar .nombre-pagina {
  font-family: "Inter", sans-serif;
  font-size: 5rem;
  margin: 0;
  background: linear-gradient(to right, #7C3AED 0%, #b5b1ea 100%);
  color: transparent;
  background-clip: text;
}

.titulo.reestablecer .nombre-pagina {
  font-family: "Inter", sans-serif;
  font-size: 5rem;
  margin: 0;
  background: linear-gradient(to right, #DB2777 0%, #fbd89e 100%);
  color: transparent;
  background-clip: text;
}

.titulo.mensaje .nombre-pagina {
  font-family: "Inter", sans-serif;
  font-size: 5rem;
  margin: 0;
  background: linear-gradient(to right, #F59E0B 0%, #fbd89e 100%);
  color: transparent;
  background-clip: text;
}

.mensaje {
  font-size: 2.5rem;
  font-weight: 700;
  padding: 1rem;
  margin-top: 2rem;
}

.titulo.confirmar .nombre-pagina {
  font-family: "Inter", sans-serif;
  font-size: 5rem;
  margin: 0;
  background: linear-gradient(to right, #2563EB 0%, #ff4343 100%);
  color: transparent;
  background-clip: text;
}

.acciones.confirmar {
  justify-content: center;
}

.dashboard {
  text-align: center;
  margin-top: 3rem;
  width: 100%;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 0.5rem;
    height: 100vh;
    text-align: start;
  }
}
.dashboard .sidebar {
  border: 1px solid #2c2c2c;
  padding: 2rem;
  background: linear-gradient(to bottom, #4B5563 50%, #2c2c2c 100%);
  height: 100%;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .dashboard .sidebar {
    box-shadow: 25px -15px 10px -20px #2c2c2c;
  }
}
.dashboard .sidebar h2 {
  color: #FFFFFF;
}
.dashboard .sidebar .sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.dashboard .sidebar .sidebar-nav a {
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem;
  transition: all 0.3s ease;
}
.dashboard .sidebar .sidebar-nav a.activo {
  background-color: #6b7280;
  border-radius: 20px;
}
.dashboard .sidebar .sidebar-nav a.activo span {
  color: #FFFFFF;
  text-align: center;
}
.dashboard .sidebar .sidebar-nav a span {
  display: block;
}
@media (min-width: 768px) {
  .dashboard .sidebar .sidebar-nav a span {
    text-align: left;
  }
}
.dashboard .sidebar .sidebar-nav a:hover {
  background-color: #e6e6e6;
  border-radius: 20px;
}
.dashboard .sidebar .sidebar-nav a:hover span {
  color: #000;
}
@media (min-width: 768px) {
  .dashboard .sidebar .sidebar-nav a:hover span {
    text-align: center;
  }
}
.dashboard .principal {
  margin: 2rem 0;
  padding: 0 1rem;
}
@media (min-width: 768px) {
  .dashboard .principal {
    flex-basis: 100%;
    margin: 0;
  }
}
.dashboard .principal .contenido {
  margin-top: 1rem;
  padding: 2rem 0;
  text-align: center;
}
.dashboard .principal .contenido .formulario {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .dashboard .principal .contenido .formulario {
    position: relative;
    min-height: 300px;
  }
  .dashboard .principal .contenido .formulario input[type=submit] {
    background-color: red;
    position: absolute;
    right: 0;
  }
}
.dashboard .principal .contenido .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
  outline: 1px solid #6b7280;
}
.dashboard .listar-proyectos {
  list-style: none;
  width: 100%;
}
.dashboard .listar-proyectos .proyectos {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background: linear-gradient(to right, #6b7280 20%, #4B5563 100%);
  padding: 1.5rem;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .dashboard .listar-proyectos .proyectos {
    flex-direction: row;
    box-shadow: 5px 10px 10px 1px #2c2c2c;
  }
}
.dashboard .listar-proyectos .proyectos .nombre-proyecto {
  font-size: 2.5rem;
  font-weight: bold;
  color: #0891B2;
}
.dashboard .listar-proyectos .proyectos div {
  display: flex;
  gap: 1rem;
  flex-basis: 100%;
}
.dashboard .listar-proyectos .proyectos div p {
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
}
.dashboard .listar-proyectos .proyectos:not(:last-child) {
  margin-bottom: 4rem;
}
.dashboard .contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.dashboard .contenedor-nueva-tarea .agregar-tarea {
  background-color: #0891B2;
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .dashboard .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
    margin: 0;
  }
}
.dashboard .contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #066981;
  cursor: pointer;
}
.dashboard .contenedor-nueva-tarea .eliminar-tarea {
  background-color: rgb(169, 0, 0);
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .dashboard .contenedor-nueva-tarea .eliminar-tarea {
    width: auto;
    margin: 0;
  }
}
.dashboard .contenedor-nueva-tarea .eliminar-tarea:hover {
  background-color: #760000;
  cursor: pointer;
}
.dashboard .listar-tareas {
  margin: 3rem auto;
  width: 100%;
}
.dashboard .listar-tareas thead tr {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
  border: 1px solid #0891B2;
}
.dashboard .listar-tareas thead tr th {
  font-weight: 700;
  width: min(150px, 100%);
}
@media (min-width: 768px) {
  .dashboard .listar-tareas thead tr th {
    font-size: 2.5rem;
  }
}
.dashboard .listar-tareas tbody tr {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
}
.dashboard .listar-tareas tbody tr:not(:last-child) {
  border-bottom: 1px solid #6b7280;
}
.dashboard .listar-tareas tbody tr td {
  width: min(150px, 100%);
}
@media (min-width: 768px) {
  .dashboard .listar-tareas tbody tr td {
    font-size: 2rem;
  }
}
.dashboard .listar-tareas tbody tr .acciones-tarea {
  display: flex;
  justify-content: space-around;
}
.dashboard .listar-tareas tbody tr .acciones-tarea a {
  cursor: pointer;
}

.sinproyectos {
  display: flex;
