  }
}

.modal {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .formulario.nueva-tarea {
  background-color: #FFFFFF;
  border-radius: 5px;
  width: min(80%, 1000px);
  padding: 3rem 2rem;
  text-align: center;
  animation: animar 1s ease 0s 1;
}
.modal .formulario.nueva-tarea legend {
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: bold;
}
.modal .formulario.nueva-tarea .campo {
  padding: 1rem;
}
.modal .formulario.nueva-tarea .campo label {
  font-weight: 700;
}
.modal .formulario.nueva-tarea .campo input {
  border: 2px solid #6b7280;
  background: #FFFFFF;
  word-break: break-word;
}
@media (min-width: 768px) {
  .modal .formulario.nueva-tarea .opciones {
    display: flex;
    justify-content: space-between;
  }
}
.modal .formulario.nueva-tarea .opciones .submit-nueva-tarea {
  background-color: #0891B2;
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 95%;
  margin: 0 auto;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .modal .formulario.nueva-tarea .opciones .submit-nueva-tarea {
    width: auto;
    margin: 0;
  }
}
.modal .formulario.nueva-tarea .opciones .submit-nueva-tarea:hover {
  background-color: #066981;
  cursor: pointer;
}
.modal .formulario.nueva-tarea .opciones .cerrar-modal {
  background-color: #F59E0B;
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 4px;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .modal .formulario.nueva-tarea .opciones .cerrar-modal {
    width: auto;
    margin: 0;
  }
}
.modal .formulario.nueva-tarea .opciones .cerrar-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
@keyframes animar {
  0% {
    transform: translateY(-230%);
  }
  100% {
    transform: translateY(0%);
  }
}

