  padding: 1rem;
  color: #000;
  font-weight: 700;
  width: 100%;
  margin: 1rem auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 2%;
}
@media (min-width: 768px) {
  .alerta-error {
    max-width: 50%;
  }
}
.alerta-error i {
  font-size: 3rem;
}

.barra {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 0.9rem 1rem;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: -5px 5px 20px -5px #000;
  }
}
.barra p {
  color: #000;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.4rem;
}
.barra p span {
  color: #0891B2;
  font-size: 2.3rem;
}