@use 'base/variables' as v;
@use 'base/mixins' as m;


.titulo {
    text-align: center;
    margin-top: 2rem;
    padding: 1rem;

    .nombre-pagina {
        @include m.degradado(v.$cyan, v.$cyan);
    }

    .descripcion-pagina {
        font-family: v.$fuente_headings;
        font-size: 2rem;
        font-weight: 900;
        color: v.$negro;
    }
}