@use 'base/variables' as v;
@use 'base/mixins' as m;


.boton {
    @include m.boton(v.$cyan, v.$blanco);

}

.boton-rojo {
    @include m.boton(lighten(v.$rojo, 10%), v.$negro);
    background-color: transparent;
    width: 100%;

}

.boton-naranja {
    @include m.boton(lighten(v.$naranja, 10%), v.$negro);
    background-color: transparent;
    width: 100%;
}