@use 'base/variables' as v;
@use 'base/mixins' as m;


.formulario {
    text-align: center;


    @include m.tablet {
        width: 100%;
        margin: 0 auto;
    }

    .campo {
        margin-bottom: 2rem;

        label {
            display: block;
            font-weight: 700;
            margin: 1rem;
            min-width: 10rem;

            @include m.tablet {
                text-align: left;
            }
        }

        input {
            display: block;
            color: v.$negro;
            background-color: lighten(v.$gris, 40%);
            border: none;
            outline: none;
            width: 95%;
            padding: 1rem;
            margin: 0 auto;
        }

        @include m.tablet {
            display: flex;
            justify-content: center;
        }

    }

}