@use 'base/variables' as v;
@use 'base/mixins' as m;


.barra {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: .9rem 1rem;
    border-radius: 10px;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        box-shadow: -5px 5px 20px -5px v.$negro;
    }

    p {
        color: v.$negro;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 1.4rem;
    }

    p span {
        color: v.$cyan;
        font-size: 2.3rem;
    }
}