@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
    margin-top: 1rem;
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3rem;
    text-align: center;

    @include m.tablet {
        flex-direction: row;

    }

    a {
        color: v.$cyan;

        &:hover {
            color: darken(v.$cyan, 10%);
        }
    }

}