@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal {
    background-color: rgb(0 0 0 /.5);


    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: flex;
    justify-content: center;
    align-items: center;





    .formulario.nueva-tarea {
        background-color: v.$blanco;
        border-radius: 5px;
        width: min(80%, 1000px);
        padding: 3rem 2rem;
        text-align: center;
        animation: animar 1s ease 0s 1;

        legend {
            margin-bottom: 2rem;
            font-size: 3rem;
            font-weight: bold;
        }

        .campo {
            padding: 1rem;

            label {
                font-weight: 700;
            }

            input {
                border: 2px solid v.$gris;
                background: v.$blanco;
                word-break: break-word;
            }
        }


        .opciones {
            @include m.tablet {
                display: flex;
                justify-content: space-between;
            }

            .submit-nueva-tarea {
                @include m.boton(v.$cyan, v.$blanco);
                margin-bottom: 1rem;
            }

            .cerrar-modal {
                @include m.boton(v.$naranja, v.$blanco);
            }
        }

        @keyframes animar {
            0% {
                transform: translateY(-230%)
            }

            100% {
                transform: translateY(0%);
            }
        }
    }



}