@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta-exito {
    position: relative;
    background-color: lighten(v.$verde, 20%);
    outline: 2px dashed lighten(v.$verde, 20%);
    padding: 1rem;
    color: v.$negro;
    font-weight: 700;
    margin: 1rem auto;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    border-radius: 2%;

    @include m.tablet {
        max-width: 50%;
    }

    i {

        font-size: 3rem;
    }

}

.alerta-error {
    position: relative;
    background-color: lighten(v.$rojo, 20%);
    outline: 2px dashed lighten(v.$rojo, 20%);
    padding: 1rem;
    color: v.$negro;
    font-weight: 700;
    width: 100%;
    margin: 1rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    border-radius: 2%;

    @include m.tablet {
        max-width: 50%;
    }

    i {

        font-size: 3rem;
    }
}